.gallery {
  width: 95%;
  overflow: auto;
  height: 80%;
  position: fixed;
  border-radius: 10px;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #616161;
}

/*.gallery__images {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}*/

/*.gallery__images img {
  height: 150px;
  width: 150px;
  padding: 0.4rem;
}*/

.gallery__row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0 4px;
  margin-bottom: 5px;
}

/* Create four equal columns that sits next to each other */
.gallery__column {
  -ms-flex: 23% 1;
      flex: 23% 1;
  max-width: 25%;
  padding: 0 4px;
}

.gallery__column img {
  width: 100%;
  margin-top: 8px;
  vertical-align: middle;
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 800px) {
  .gallery__column {
    -ms-flex: 48% 1;
        flex: 48% 1;
    max-width: 50%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .gallery__column {
    -ms-flex: 100% 1;
        flex: 100% 1;
    max-width: 100%;
  }
}



